.btn, .btn:link, .btn:visited {
  color: #fff;
  background-color: #293335;
  border: 1px solid #293335;
  border-radius: 10rem;
  margin-top: 2.2rem;
  padding: 1.5rem 4rem;
  font-size: 1.8rem;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
  position: relative;
  box-shadow: 0 1rem 2rem #0000002b;
}

@media (width >= 768px) {
  .btn, .btn:link, .btn:visited {
    font-size: 2rem;
  }
}

.btn:after {
  content: "";
  z-index: -100;
  width: 100%;
  height: 100%;
  color: inherit;
  background: inherit;
  border-radius: 10rem;
  transition: all .4s;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 2rem 3rem #0000002b;
}

.btn:hover:after {
  opacity: 0;
  transform: scaleX(1.4)scaleY(1.6);
}

.btn:active {
  transform: translateY(-1px);
  box-shadow: 0 1rem 2rem #0000002b;
}

